

.video__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  /* max-width: 1120px; */
  /* width: 90%; */
  margin: 0 auto;
  background-color: white;
}

.video__wrapper {
  position: relative;
  margin: 50px;
}

/* .video__items {
  margin-bottom: 24px;
} */

.video__item {

  width: 900px;
  height: 500px;
  /* border-radius: 10px; */
}

.video__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(69, 81, 102, 0.8);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.video__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.video__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #1f98f4;
  box-sizing: border-box;
}

.video__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}


@media only screen and (max-width: 1024px) {
  .video__item {
    width: 100%;
    height: 200px;
  }
}
